import { useState } from "react";
import { useSelector } from "react-redux";
import { Grommet, Box, ResponsiveContext } from "grommet";
import { grommet } from "grommet/themes";
import { deepMerge } from "grommet/utils";
import themeMods from "./themeMods";

import Header from "./features/nav/Nav";
import Main from "./Main";
import Side from "./Side";
import { Docs } from "./features/docs/Docs";
import { Dash } from "./features/dash/Dash";
import DashSide from "./features/dash/DashSide";
import { DashDocs, DashDoc } from "./features/dash/DashDoc";
import { Messages } from "./features/dash/Messages";

const theme = deepMerge(grommet, themeMods);

const App = () => {
  const { main, side } = useSelector((state) => state.nav);

  const [darkMode, setDarkMode] = useState(true);

  const mainCol = () => {
    switch (main) {
      case "docs":
        return <Docs />;
      case "dash":
        return <Dash />;
      case "dashdocs":
        return <DashDocs />;
      case "dashdoc":
        return <DashDoc />;
      case "messages":
        return <Messages />;
      default:
        return <Main />;
    }
  };

  const sideCol = () => {
    switch (side) {
      case "dash":
        return <DashSide />;
      case "side":
        return <Side />;
      default:
        return null;
    }
  };

  console.log("sideCol: ", side);

  return (
    <Grommet full theme={theme} themeMode={darkMode ? "dark" : "light"}>
      <Box>
        <Header darkMode={darkMode} setMode={setDarkMode} />
      </Box>
      <Box align="center">
        <ResponsiveContext.Consumer>
          {(size) => (
            <Box
              direction="row-responsive"
              align="center"
              gap="medium"
              pad="small"
              // overflow={{
              //   horizontal: "auto"
              // }}
            >
              <Box
           
                    width={size !== "small" && side ? "65%" : "100%"}
       
                        key="1"
              
              >
                {mainCol()}
              </Box>
              <Box width={size !== "small" ? "35%" : null} key="2">
                {sideCol()}
              </Box>
            </Box>
          )}
        </ResponsiveContext.Consumer>
      </Box>
      <Box pad={{ vertical: "large" }}></Box>
    </Grommet>
  );
};

export default App;
