import { Text, DataTable } from "grommet";
import { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { showDoc } from "../dash/dashStore";
import { uploadFile } from "../docs/docsStore";

export const Docs = ({ tempfiles }) => {
  const [select, onSelect] = useState(null);
  const { files } = useSelector((state) => state.docs);
  const dispatch = useDispatch();

  useEffect(() => {
    console.log(tempfiles);
  }, [tempfiles]);

  useEffect(() => {
    if (select) {
      select.map((fn) =>
        dispatch(uploadFile(files.find((f) => f.name === fn)))
      );
    }
  }, [select, dispatch, files]);

  const columns = [
    {
      property: "name",
      primary: true,
      header: <Text>File</Text>,
      render: (datum) =>
        datum.name
          .replace("-", " ")
          .replace(/\.[^.]+$/, "")
          .substring(0, 18),
    },
    {
      property: "size",
      header: <Text>KB</Text>,
      render: (datum) => datum,

      //   {
      //     let value = files[datum.name].progress;
      //     return !value ? (
      //       Math.round(datum.size / 1024)
      //     ) : (
      //       <Meter
      //         size="small"
      //         values={[{ value, color: value === 100 ? "accent-1" : "black" }]}
      //       />
      //     );
      //   },
    },
    {
      property: "lastModified",
      header: <Text>Date</Text>,
      render: (datum) =>
        new Date(datum.lastModified).toLocaleDateString("en-GB"),
    },
  ];

  return (
    <DataTable
      columns={columns}
      data={tempfiles}
      step={10}
      select={select}
      onSelect={onSelect}
      onClickRow={(event) => dispatch(showDoc(event.datum))}
    />
  );
};
