import { createSlice } from "@reduxjs/toolkit";
import firebase from "../../firebase";
const store = firebase.storage();

const slice = createSlice({
  name: "docs",
  initialState: {
    user: "x",
    error: null,
    files: [
      {
        name: "Title Register.pdf",
        url:
          "https://firebasestorage.googleapis.com/v0/b/docs-conveyanceme/o/x%2FTitle%20Register.pdf?alt=media&token=39ba08f3-afd0-45e4-9e91-84cc5b7b30b1",
        messages: [],
      },
      {
        name: "TR1 Transfer of Registered Title.pdf",
        url:
          "https://firebasestorage.googleapis.com/v0/b/docs-conveyanceme/o/x%2FTR1%20Transfer%20of%20Registered%20Title.pdf?alt=media&token=45149eaa-f49b-49b7-b5a8-96af0951f68c",
      },
      {
        name: "TA7 Leasehold Information Form.pdf",
        url:
          "https://firebasestorage.googleapis.com/v0/b/docs-conveyanceme/o/x%2FTA7%20Leasehold%20Information%20Form.pdf?alt=media&token=7255b419-31b3-4158-aa51-66748028ddc9",
      },
      {
        name: "TA10 Fittings and Contents Form.pdf",
        url:
          "https://firebasestorage.googleapis.com/v0/b/docs-conveyanceme/o/x%2FTA10%20Fittings%20and%20Contents%20Form.pdf?alt=media&token=75d86206-90be-42db-af51-b56bf0fe264c",
      },
      {
        name: "TA6 Property Information Form.pdf",
        url:
          "https://firebasestorage.googleapis.com/v0/b/docs-conveyanceme/o/x%2FTA6%20Property%20Information%20Form.pdf?alt=media&token=284ef0f3-b8de-44e5-b6a4-c3389cb8ff22",
      },
    ],
  },
  reducers: {
    uploadProgress: ({ files }, { payload }) => {
      const { name, progress } = payload;
      let exists = files.find((file) => file.name === name);
      exists
        ? Object.assign(exists, { progress })
        : files.push({
            name,
            progress,
          });
    },
    uploadURL: ({ files }, { payload }) => {
      const { name, url } = payload;
      let exists = files.find((file) => file.name === name);
      exists
        ? Object.assign(exists, { url })
        : files.push({
            name,
            url,
          });
    },
    uploadFail: (state, { payload }) => {
      state.error = payload.error;
    },
  },
});
export default slice.reducer;

const { uploadFail, uploadURL, uploadProgress } = slice.actions;
export const uploadFile = (file) => async (dispatch) => {
  let fileRef = store.ref("x/" + file.name);
  let uploader = fileRef.put(file);
  try {
    uploader.on(
      "state_changed",
      ({ ref, bytesTransferred, totalBytes }) => {
        const progress = Math.round((bytesTransferred / totalBytes) * 100);
        const name = ref.name;
        console.log(name, progress);
        dispatch(uploadProgress({ name, progress }));
      },
      {},
      () =>
        fileRef
          .getDownloadURL()
          .then((url) => dispatch(uploadURL({ name: file.name, url })))
    );
  } catch (error) {
    dispatch(uploadFail(error));
  }
};
