import { Card as CardHolder, CardBody, CardFooter, Box, Text } from "grommet";

const Card = ({ title, subTitle, align, children, footer, size }) => {
  return (
    <CardHolder key={title} width={size}>
      <CardBody>
        <Box align={align || "center"}>
          <Box>
            <Text size="large" weight="bold">
              {title}
            </Text>
            <Text>{subTitle}</Text>
          </Box>
        </Box>
        <Box align="center">
          {children}
        </Box>
      </CardBody>
      {footer && (
        <CardFooter>
          <Text size="xsmall">{footer}</Text>
        </CardFooter>
      )}
    </CardHolder>
  );
};

export default Card;
