import { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Document } from "../../components/Document";
import Card from "../../components/Card";
import { showDoc } from "../dash/dashStore";
import { navTo } from "../nav/navStore";
import { Box, Button, Anchor, ResponsiveContext } from "grommet";
import { Close, Previous, Next } from "grommet-icons";

export const DashDoc = () => {
  const dispatch = useDispatch();
  const { showdoc } = useSelector((state) => state.dash);
  const [turnPage, setTurnPage] = useState(null);

  useEffect(() => {
    setTurnPage(null);
  }, [turnPage]);

  return (
    <Box align="center" pad={{ top: "large" }}>
      <Box direction="row" width="100%">
        <Button
          align="start"
          icon={<Previous />}
          onClick={() => setTurnPage(false)}
        />
        <Button icon={<Next />} onClick={() => setTurnPage(true)} />
        <Box flex />
        <Button
          icon={<Close />}
          onClick={() => {
            dispatch(navTo({ main: "dashdocs", side: null }));
            dispatch(showDoc(null));
          }}
        />
      </Box>
      <Box>
        <Document pdf={showdoc} turnPage={turnPage} height={1000} />
      </Box>
    </Box>
  );
};

export const DashDocs = () => {
  const { files } = useSelector((state) => state.docs);
  const [select, onSelect] = useState(null);
  const [turnPage, setTurnPage] = useState(null);

  useEffect(() => {
    setTurnPage(null);
  }, [turnPage]);

  return (
    <Box pad={{ top: "medium" }} align="center">
      <ResponsiveContext.Consumer>
        {(size) =>
          files.map((file, i) => (
            <>
              <Anchor key={i} onClick={() => onSelect(i)} margin="small">
                <Card
                  key={i}
                  title={
                    select === i ? (
                      <Box key={i} direction="row">
                        <Button
                          align="start"
                          icon={<Previous />}
                          onClick={() => setTurnPage(false)}
                        />
                        <Button
                          icon={<Next />}
                          onClick={() => setTurnPage(true)}
                        />
                      </Box>
                    ) : null
                  }
                  subTitle=""
                  children={
                    <Document
                      pdf={file}
                      height={
                        select === i ? (size === "small" ? 600 : 1100) : 250
                      }
                      turnPage={turnPage}
                    />
                  }
                  footer={
                    file.name
                      ? file.name.replace(/-|_/g, " ").substring(0, 38)
                      : null
                  }
                  size={
                    select === i
                      ? size === "small"
                        ? "medium"
                        : "large"
                      : "small"
                  }
                />
              </Anchor>
            </>
          ))
        }
      </ResponsiveContext.Consumer>
    </Box>
  );
};
