import { Button, Box, Header as Head, Menu, ResponsiveContext } from "grommet";
import { Home, Apps, Copy, Moon, Sun } from "grommet-icons";

import { useDispatch } from "react-redux";
import { navTo } from "./navStore";

const Nav = ({ darkMode, setMode }) => {
  const dispatch = useDispatch();

  const darkModeBtn = (
    <Button
      onClick={() => setMode((darkMode = !darkMode))}
      icon={darkMode ? <Sun color="background" /> : <Moon color="background" />}
    />
  );

  const burgerMenuItems = [
    {
      label: "Home",
      icon: <Home />,
      onClick: () => dispatch(navTo({ main: "main", side: "side" })),
    },
    {
      label: "Docs",
      icon: <Copy />,
      onClick: () => dispatch(navTo({ main: "dashdocs", side: null })),
    },
  ];

  return (
    <Head background="gradient" pad="medium" height="xsmall" round="medium">
      <ResponsiveContext.Consumer>
        {(size) =>
          size !== "small" ? (
            <>
              <Button
                margin={{ left: "medium" }}
                plain
                onClick={() => dispatch(navTo({ main: "main", side: "side" }))}
                icon={<Home size="40" color="background" />}
                hoverIndicator={true}
                tip={{ content: "home screen" }}
              />
              <Button
                icon={<Copy color="background" />}
                reverse
                plain
                onClick={() =>
                 
                 
                  dispatch(navTo({ main: "dashdocs", side: null }))
                
                
                }
              />
              {/* <Button
                icon={<Chat color="background" />}
                onClick={() => dispatch(navTo({ main: "messages", side: "" }))}
              /> */}
              <Box width="small"></Box>
              <Box justify="end">{darkModeBtn}</Box>
            </>
          ) : (
            <Menu
              a11yTitle="Navigation Menu"
              dropProps={{ align: { top: "bottom", right: "right" } }}
              icon={<Apps color="background" />}
              items={burgerMenuItems}
              dropBackground="#c3c3c3"
              size="medium"
            />
          )
        }
      </ResponsiveContext.Consumer>
    </Head>
  );
};

export default Nav;
